  async login(user: User) {
    this.log = true;
    try {
      const result = await this.afAuth.auth.signInWithEmailAndPassword(user.email, user.password);
      if (result) {
        console.log('result',result);
        this.loadUserInfo();
       this.navCtrl.setRoot(TabsPage);
      }
    }
  catch(e){
    console.error(e);
    }
  }

